                },
                NIVEL: {
                    columns: ['Date', 'Nivel'],
                    rows: []
                },
                TEMP: {
                    columns: ['Date', 'Temperatura'],
                    rows: []
                },
                vals: [],
                loader: null,
                loading1: false,
                loading2: false,
                loading3: false,
                loading4: false,
                loading5: false,
                loading6: false,
                menu1: false,
                menu2: false,
                date1: '',
                date2: '',
                dateFormatted1: '',
                dateFormatted2: '',
                init1: true,
                init2: true,
                json_fields_pH: {
                    'Data': 'Date',
                    'pH': 'pH'
                },
                json_fields_ec: {
                    'Data': 'Date',
                    'Condutividade': 'Condutividade'
                },
                json_fields_od: {
                    'Data': 'Date',
                    'OxigÃªnio Dissolvido': 'Oxigenio'
                },
                json_fields_turb: {
                    'Data': 'Date',
                    'Turbidez': 'Turbidez'
                },
                json_fields_nivel: {
                    'Data': 'Date',
                    'Nivel': 'Nivel'
                },
                json_fields_temp: {
                    'Data': 'Date',
                    'Temperatura': 'Temperatura'
                },
                hasData: false,
                okph: false,
                okec: false,
                okod: false,
                okturb: false,
                oknivel: false,
                oktemp: false,
                preDate1: '',
                preDate2: ''
            }
        },
        firestore (){
            return{
                valspH: db.collection(this.$route.params.Pid).doc('pH').collection('mediÃ§Ãµes'),
                valsEc: db.collection(this.$route.params.Pid).doc('Condutividade').collection('mediÃ§Ãµes'),
                valsOd: db.collection(this.$route.params.Pid).doc('Oxigenio').collection('mediÃ§Ãµes'),
                valsTu: db.collection(this.$route.params.Pid).doc('Turbidez').collection('mediÃ§Ãµes'),
                valsNi: db.collection(this.$route.params.Pid).doc('Nivel').collection('mediÃ§Ãµes'),
